body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html body{
  margin-top: 10%;
  height: 70vh;
  background: -moz-linear-gradient(to bottom,  rgb(31, 25, 26) 10%,rgb(143, 6, 38) 28%,rgba(109,0,25,1) 50%); 
  background: -webkit-linear-gradient(to bottom,  rgb(31, 25, 26) 10%,rgb(143, 6, 38) 28%,rgba(109,0,25,1) 50%); 
  background: linear-gradient(to bottom,  rgb(31, 25, 26) 10%,rgb(143, 6, 38) 28%,rgba(109,0,25,1) 50%); 
}
